.promotion-search__header {
    display: flex ;
    justify-content: space-between ;
    align-items: center ;
    margin-bottom: 20px ;
}

.promotion-search__header h1 {
    text-transform: uppercase;
    color: #ccc;
}

.promotion-search__input {
    display: flex;
    height: 42px;
    border: 1px solid #ccc;
    border-radius: 8px;
    width: 100%;
    margin-bottom: 20px;
    padding: 0 10px;
}