.promotion-card {
    border-radius: 8px;
    border: 1px solid #c3c3c3;
    box-shadow: 0 2px 7px 0 rgba(0,0,0, 0.15);
    display: flex;
    padding: 15px;
    align-items: center;
    position: relative;
}

.promotion-card__image{
    width: 100px;
    margin-right: 20px;
}
.promotion-card__info{
    flex-grow: 1;
}

.promotion-card__title{
    font-size: 16px;
    color:#2c3e50;
}

.promotion-card__price{
    font-size: 26px;
    font-weight: 700;
    color: #3498d8;
}

.promotion-card__footer{
    display: flex;
    align-items: center;
}

.promotion-card__comment{
    color: #95a5a6;
    font-weight: 700;
    max-width: 345px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow:hidden;
}
.promotion-card__comments-count{
    margin-left: auto;
    margin-right: 20px;
    color: #3498d8;
    background-color: unset;
    border: unset;
    white-space: nowrap;
}


.promotion-card__edit-button{
    
    margin-left: 10px;
    
}

.promotion-card__delete-button {
    border: unset;
    background-color: unset;
    font-size: 18px;
    position: absolute;
    top: 8px;
    right: 8px;

}
